<script>
  import {DatetimePicker, Navbar, TabItem, Popup, Indicator, MessageBox} from 'mint-ui';
  import ElementUI from 'element-ui'
  import utils from '@/components/utils'
  import moment from 'moment'


  let df = 'YYYY-MM-DD HH:mm';

  export default {
    data(){
      return {
//        tokenHeader: {
//          charset: 'utf-8',
//          openId: this.getCookie('openId')
//        },
        selectInfo: 'a', // nav
        popImgSrc: '', // 
        popupVisible: false, // 
        imgSrc: {
          ico_success: require('../../assets/ico_success.png'),
          ico_error: require('../../assets/ico_error.png'),
        },
        searchApplyRecord: [], //
        leaveSuccess: false, //
        alertMessage: '',//
        alertSuccessImage: false,//
        codeSuccess: '',//
        hasNextperson: true,
        approvalTypeObj: [],
        applyData:{},
        approveAllData:{},
        selectperData:'',
        showperson: false,  //
        hasNextperson: false, //
        currentItem: {}
      };
    },
    created: function () {
      this.changeShow('0');
      // this.approvalperson('1');
    },
    watch: {},
    methods: {
      //
      noNextperson(){
        let arr = [];
        arr.push(this.currentItem.uid);
        let params = {
          applyUids: arr,  //uid
          status: 1, //013
          nextApprover: '', //uid 
          flowWhy: this.currentItem.why, //
        };
        let url = '/api/v1.0/client/agree';
        this.$http.post(url, params).then(response => { //
          // Indicator.close();
          this.hasNextperson = false;
          if (response.body.code === 200) {
            this.changeShow('0');
            MessageBox('', '');
          } else {
            MessageBox('', '');
          }
        }, response => {
//          console.log('error callback');
        });
      },
      // 
      selectperson(row, event, column){
        this.selectperData = row.NAME;
        this.hasNextperson = false;
        this.showperson = false;

        let arr = [];
        arr.push(this.currentItem.uid);
        let params = {
          applyUids: arr,  //uid
          status: 0, //013
          nextApprover: row.UID, //uid 
          flowWhy: this.currentItem.why, //
        };
        let url = '/api/v1.0/client/agree';
        this.$http.post(url, params).then(response => { //
          // Indicator.close();
          if (response.body.code === 200) {
            this.changeShow('0');
            MessageBox('', '');
          } else {
            MessageBox('', '');
          }
        }, response => {
//          console.log('error callback');
        });

      },
      //
      approvalperson(configType){
        this.$http.get('/api/v1.0/client/findReporter/'+configType).then(response => {
          let data = response.body.result;
          this.approvalTypeObj = data;
          console.log(111111111);
          console.log(this.approvalTypeObj);
          // this.applyData.applicant = data.UID;   //uid
          // this.applyData.category = data.WAY;   // ,12
          // this.approveAllData.category = data.WAY;
          // if(data.WAY == '1'){
          //   this.applyData.currentApprover = data.UID;
          //   this.approveAllData.currentApprover = data.UID;
          //   this.approveAllData.email = '';
          // }else if(data.WAY == '2'){
          //   this.applyData.email = data.NAME;
          //   this.approveAllData.currentApprover = '';
          //   this.approveAllData.email = data.UID;
          // }

        }, response => {
          //console.log('error callback');
        });
      },
      //
      gotodetail(){
        this.$router.push({path: '/approveDetail'});
      },
      //
      applyState(state){
        let status;
        switch (state) {
          case 0:
            status = '';
            break;
          case 1:
            status = '';
            break;
          case 2:
            status = '';
            break;
          case 3:
            status = '';
            break;
        }
        return status;
      },
      // 
      overtimeNum(num){
        let arr = ['', '', '', '', '', '', '', '', '', ''];
        return arr[num];
      },
      changeShow(val){ //
        this.$http.post('/api/v1.0/client/queryOtherApplys', {
          status: val,
          pageSize: 100,
          pageNumber: 1
        }).then(response => {
          if (response.body.code === 200) {
            // this.searchApplyRecord = response.body.result;
            let data = response.body.result.list;
            for(let i = 0; i < data.length; i++){
              let item = data[i];
              for(let j = 0; j < item.approvalFields.length; j++){
                let list = item.approvalFields[j];
                if(list.fieldType == '7' && list.approvalValues.length > 0){
                  let timearr = [];
                  let number = Math.floor(list.approvalValues.length/2);
                  if(parseInt(list.approvalValues[0].term) > 0){
                    number = parseInt(list.approvalValues[0].term) + number;
                  }
                  for(let m = 0; m < number; m++){
                    let timeobj = {
                      startTime: '',
                      endTime: ''
                    };
                    for(let n = 0; n < list.approvalValues.length; n++){
                      let detail = list.approvalValues[n];
                      if(detail.term == m && detail.sortnum == 0){
                        timeobj.startTime = detail.value;
                      }else if(detail.term == m && detail.sortnum == 1){
                        timeobj.endTime = detail.value;
                        timearr.push(timeobj);
                      }
                    }
                  }
                  list.periodarr = timearr;
                }
              }
            }
            this.searchApplyRecord = data;

          }
        }, response => {
//          console.log('error callback');
        });
      },
      /*changeShow(val){ //
        // this.$http.get('/api/v1.0/client/approval/findComplete').then(response => { //
        this.$http.get('/api/v1.0/client/approval/findComplete').then(response => { //
          if (response.body.code === 200) {
            this.searchApplyRecord = response.body.result;
          }
        }, response => {
//          console.log('error callback');
        });
      },*/
      // 
      lookImages(imgSrc){
        if (imgSrc) this.popImgSrc = imgSrc;
        else this.popImgSrc = '';
        this.popupVisible = true;
      },
      // 
      closeImage(){
        this.popupVisible = false;
      },
      // 
      datefmt(str) {
        if (str) return moment(str).format(df);
        else return '';
      },
      //
      closeAlert(){
        this.leaveSuccess = false;
        if (this.codeSuccess === 200) {
          this.$router.push({path: '/signCard'});
        }
      },
      //
      isPass(item, type){
        let url, text;
        if (type === 1) {//
          url = '/api/v1.0/client/agree';
          text = '';
        } else if (type = 2) {//
          url = '/api/v1.0/client/refuse/' + item.uid;
          text = '';
        }
        MessageBox.confirm(text, '').then(action => {

          // Indicator.open('...');
          let arr = [];
          arr.push(item.uid);
          if (type === 1) {//
            if(item.configType == '1'){  //0 ,  1 
              this.currentItem = item;
              this.hasNextperson = true;
              this.approvalperson(item.configType);
            }else if(item.configType == '0'){  //
              let params = {
                // applyUid: item.uid,  //uid
                applyUids: arr,  //uid
                status: 1, //013
                nextApprover: "", //uid 
                flowWhy: item.why, //
                // currentLoginUser: item.parentUid //uid 
              };
              this.$http.post(url, params).then(response => { //
                // Indicator.close();
                if (response.body.code === 200) {
                  this.changeShow(-1);
                  MessageBox('', '');
                } else {
                  MessageBox('', '');
                }
              }, response => {
//                console.log('error callback');
              });
            }

          } else if (type = 2) {//
            this.$http.get(url).then(response => { //
              // Indicator.close();
              if (response.body.code === 200) {
                this.changeShow(-1);
                MessageBox('', '');
              } else {
                MessageBox('', '');
              }
            }, response => {
//          console.log('error callback');
            });
          }


        });
      },
      //
      queryOverworkTime(arr, type){
        let time = 0;
        arr.map(item => {
          if (item.type === type) {
            time = item.time;
          }
        });
        return time;
      }
    },
    components: {},
  }
</script>

