      // 
      formatPunchState(state, area, time, isToday, type){
        let show = '';
        if (time) {
          if (type) {
            if (state === 0) {
              show = '';
            } else if (state === 1) {
              show = '';
            } else if (state === 2) {
              show = '';
            }
          } else {
            if (state === 0) {
              show = '';
            } else if (state === 1) {
              show = '';
            } else if (state === 2) {
              show = '';
            } else if (state === 3) {
              show = '';
            }
          }
          if (area) {
            show = show + '()';
          }
        } else {
          if (isToday) {
            show = '';
          } else {
            show = '';
          }
        }
        return show;
