          this.startTimeValue1 = displaytime ? displaytime : new Date();
          this.$refs.picker0.open();
        }else if(fieldType == '7'){
          if(type == 0){
            if(this.applyWorkRefAll[this.uid][this.pos].startTime){
              this.startTimeValue1 = this.applyWorkRefAll[this.uid][this.pos].startTime;
            }else{
              this.startTimeValue1 = new Date();
            }
            this.$refs.picker0.open();
          }else if(type == 1){
            if(this.applyWorkRefAll[this.uid][this.pos].endTime){
              this.endTimeValue1 = this.applyWorkRefAll[this.uid][this.pos].endTime;
            }else{
              this.endTimeValue1 = new Date();
            }
            this.$refs.picker1.open();
          }
        }
      },
      // 
      overtimeNum(num){
        let arr = ['', '', '', '', '', '', '', '', '', ''];
        return arr[num];
      },
      //
      handerDataSubmit(){

        // this.showMsg("", 1);
        // return false;

        // console.log(this.applyWorkRef);
        // return false;

        Indicator.open('...');
        let approvalValues = [];
        for( let i = 0; i < this.fields.length; i++){
          let item = this.fields[i];

          //
          /*if(item.fieldType == "0" || item.fieldType == "1" || item.fieldType == "2" || item.fieldType == "6"){ //
            if(item.value == '' || item.value ==undefined){
              this.showMsg(item.fieldHint,-1);
              return false;
            }
          }else if(item.fieldType == "3" || item.fieldType == "4" || item.fieldType == "5"){ //
            if(this.confItemsval[item.uid] == [] || this.confItemsval[item.uid].length == 0){
              this.showMsg(item.fieldHint,-1);
              return false;
            }
          }else if(item.fieldType == "7"){ //
            if(this.applyWorkRef[0].startTime == '' || this.applyWorkRef[0].startTime == ''){
              this.showMsg(item.fieldHint,-1);
              return false;
            }

          }else if(item.fieldType == "8"){ //
            if(this.confItemsval[item.uid] == [] || this.confItemsval[item.uid].length == 0){
              this.showMsg(item.fieldHint,-1);
              return false;
            }
          }*/


          if(item.fieldType != "7" && item.fieldType != "3" && item.fieldType != "4" && (item.fieldType != "5" || item.code == "outType" || item.code =="leaveType")){
            approvalValues.push({
              approvalFieldUid : item.uid,
              value : item.value,
              term : item.term,
              sortnum : item.sortnumtmp
            });
          }else{
            if(item.fieldType == "3" || (item.fieldType == "5" && item.code != "outType")){  //
              approvalValues.push({
                approvalFieldUid : item.uid,
                value : this.confItemsval[item.uid],
                term : 0,
                sortnum : 0
              });
            }else if(item.fieldType == "4"){  //
              for(let j = 0; j < this.confItemsval[item.uid].length; j++){
                approvalValues.push({
                  approvalFieldUid : item.uid,
                  value : this.confItemsval[item.uid][j],
                  term : 0,
                  sortnum : j
                });
              }
            }
          }

        }

        //item.fieldType="7"
        let periodarr = [];
        for(let i = 0; i < this.applyWorkRef.length; i++){
          let item = this.applyWorkRef[i];
          for(let j = 0; j < 2; j++){
            let timecurr;
            if(j == 0){
              timecurr = item.startTime;
            }else if(j == 1){
              timecurr = item.endTime;
            }
            periodarr.push({
              approvalFieldUid : item.uid,
              value : timecurr,
              term : i,
              sortnum : j
            });
          }
        }
        console.log(approvalValues);
        this.applyData.approvalValues = approvalValues.concat(periodarr);

        console.log(this.applyData);
        console.log(33333);

        return false;
        this.$http.post('/api/v1.0/client/apply', this.applyData).then(response => { //
          Indicator.close();//
          this.codeSuccess = response.body.code;
          this.leaveSuccess = true;
          if (response.body.code === 200) {
            this.alertSuccessImage = true;
            this.alertMessage = response.body.message;
          } else {
            this.alertSuccessImage = false;
            this.alertMessage = response.body.message;
          }
        }, response => {
//          console.log('error callback');
        });

      },
      qingjiaclick(value, index){
        console.log("");
        console.log(value);

        this.applyData.leaveUid = value.LEAVE_INFO_UID;
        this.fields[index].value = value.NAME;
        this.selectHoliday = value;
      },
      waichuclick(value, index){
        console.log(value);
        this.fields[index].value = value.name;
        this.selectHoliday = value;
      },
      // 
      lookImages(imgSrc){
        if (imgSrc) this.popImgSrc = imgSrc;
        else this.popImgSrc = '';
        this.popupVisible = true;
      },
      // 
      closeImage(){
        this.popupVisible = false;
      },
      // 
      datefmt(str) {
        if (str) return moment(str).format(df);
        else return '';
      },
      //
      closeAlert(){
        this.leaveSuccess = false;
        if (this.codeSuccess === 200) {
          this.$router.push({path: '/signCard'});
        }
      },
      // 
      passportUrlOk(res, file) {
        if (res.code === 200) {
          // this.applyData.image = res.result;

          for(let i = 0; i< this.fields.length; i++){
            let item = this.fields[i];
            if(item.fieldType == '8'){
              item.value = res.result;
            }
          }
        }
      },
      // 
      beforePassportUrl(file) {
        let isImage = utils.isImage(file);
        let isInSize = utils.isInSize(file, 5);
        if (isImage && isInSize) {
          this.passportUrlErrFlag = false;
        } else {
          this.passportUrlErrFlag = true;
        }
        return isImage && isInSize;
      },
    },
    components: {
      fullCalendar
    }
  }
</script>

